<template>
	<div style="width: 100%;height: 100%; background: #fff;">
		<div class="header">
			<i class="el-icon-circle-close-outline" @click="upper"></i>
			<span v-text="title" @click="upper"></span>
		</div>
		<div class="nav">
			<el-form ref="form" :model="form" label-width="100px" style='width: 70%;'>
				<el-form-item label="品牌">
					<el-select v-model="form.brandcode" placeholder="请选择系统">
						<el-option v-for="item in brandName" :key="item.brandcode" :label="item.dealername" :value="item.brandcode"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="经销商全称">
					<el-input v-model="form.dealername" placeholder="请输入经销商全称"></el-input>
				</el-form-item>
				<el-form-item label="经销商简称">
					<el-input v-model="form.dealershtname" placeholder="请输入经销商简称"></el-input>
				</el-form-item>
				<el-form-item label="地址">
					<el-input v-model="form.addr" placeholder="请输入地址"></el-input>
				</el-form-item>
				<el-form-item label="省份">
					<el-select v-model="form.prov" placeholder="请选择省份" @change="chose">
						<el-option v-for="item in provList" :key="item.regionCode" :label="item.regionName" :value="item.regionCode"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="城市">
					<el-select v-model="form.city" placeholder="请选择城市">
						<el-option v-for="item in cityList" :key="item.regionCode" :label="item.regionName" :value="item.regionCode"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="联系人">
					<el-input v-model="form.linkman1" placeholder="请输入联系人"></el-input>
				</el-form-item>
				<el-form-item label="联系电话">
					<el-input v-model="form.linktel1" placeholder="请输入联系电话"></el-input>
				</el-form-item>

				<el-form-item label="联系邮箱">
					<el-input v-model="form.email" placeholder="请输入联系邮箱"></el-input>
				</el-form-item>
				<!--<el-form-item label="MDM经销商">-->
				<!--<el-input v-model="form.name" placeholder="请输入MDM经销商"></el-input>-->
				<el-form-item label="MDM经销商" :label-width="formLabelWidth" style='width: 66%;'>
					<el-select v-model="form.dealerCodes" multiple filterable remote placeholder="请输入经销商名称" :remote-method="remoteMethod" :loading="loading" clearable @change="choise" style='width: 100%;'>
						<el-option v-for="item in options4" :key="item.dealerCode" :label="item.dealerName" :value="item.dealerCode">
						</el-option>
					</el-select>
				</el-form-item>
				<!--</el-form-item>-->
			</el-form>

		</div>
		<div class="headerRight">
			<SolidButton :type='"hollow"' :text='"取消"' @click="upper"></SolidButton>
			<SolidButton :type='"solid"' :text='"提交"' @click="submit"></SolidButton>
		</div>
	</div>
</template>

<script>
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';

	export default {
		components: {
			SolidButton
		},
		data() {
			return {
				title: '',
				message: '',
				form: {
					id: '',
					brandcode: '',
					dealername: '',
					dealershtname: '',
					addr: '',
					prov: '',
					city: '',
					linkman1: '',
					linktel1: '',
					email: '',
					dealerCodes: '',
				},
				brandName: [],
				provList: [],
				cityList: [],
				formLabelWidth: '100px',
				loading: false,
				options4: [], //经销商模糊搜索数据数组
				list: '',
			}
		},
		methods: {
			//初始化数据
			loadData() {
				var self = this;
				//下拉
				self.$api.post('/crm/dealer/getBrandCodes', {}, r => {
					self.brandName = r.repData
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})
				//初始化省份
				self.$api.post('/crm/region/provinces', {}, r => {
					self.provList = r.repData
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})

				//带过来的数据
				if(this.list != undefined) {
					this.title = '修改',
						this.message = '修改成功'
					this.form.id = this.list.id;
					this.form.brandcode = this.list.brandcode;
					this.form.dealername = this.list.dealername;
					this.form.dealershtname = this.list.dealershtname;
					this.form.addr = this.list.addr;
					this.form.prov = this.list.prov;
					this.form.city = this.list.city;
					this.form.linkman1 = this.list.linkman1;
					this.form.linktel1 = this.list.linktel1;
					this.form.email = this.list.email;
					var arr = [];
					for(var i = 0; i < this.list.dealerOmsList.length; i++) {
						arr.push(this.list.dealerOmsList[i].dealerCode)
						this.options4.push({
							dealerName: this.list.dealerOmsList[i].dealerName,
							dealerCode: this.list.dealerOmsList[i].dealerCode
						})
					}
					this.form.dealerCodes = arr;
				} else {
					this.title = '新增经销商';
					this.message = '新增成功'
				}
			},
			//返回
			upper() {
				this.$router.push({
					path: "/DealerManage",
					query: {}
				})
			},
			//提交
			submit() {
				var self = this
				if(this.form.brandcode == '') {
					this.$message({
						message: '请选择品牌',
						type: 'warning'
					});
					return;
				}
				if(this.form.dealername == '') {
					this.$message({
						message: '请输入经销商全称',
						type: 'warning'
					});
					return;
				}
				if(this.form.dealershtname == '') {
					this.$message({
						message: '请输入经销商简称',
						type: 'warning'
					});
					return;
				}
				if(this.form.addr == '') {
					this.$message({
						message: '请输入地址',
						type: 'warning'
					});
					return;
				}
				if(this.form.prov == '') {
					this.$message({
						message: '请选择省份',
						type: 'warning'
					});
					return;
				}
				if(this.form.city == '') {
					this.$message({
						message: '请选择城市',
						type: 'warning'
					});
					return;
				}
				if(this.form.linkman1 == '') {
					this.$message({
						message: '请输入联系人',
						type: 'warning'
					});
					return;
				}
				if(this.form.linktel1 == '') {
					this.$message({
						message: '请输入联系人电话',
						type: 'warning'
					});
					return;
				}
				if(this.form.email == '') {
					this.$message({
						message: '请输入联系邮箱',
						type: 'warning'
					});
					return;
				}
				if(this.form.dealerCodes == '') {
					this.$message({
						message: '请选择MDM经销商',
						type: 'warning'
					});
					return;
				}
				this.$api.post('/crm/dealer/insertOrUpdate', this.form, r => {
					this.$message({
						message: this.message,
						type: 'success'
					});
					setTimeout(function() {
						self.upper();
					}, 1000)
				}, e => {
					console.log(e);
				})
			},
			onSubmit() {
				console.log('submit!');
			},
			//选择省份加载市
			chose(o) {
				this.$api.post('/crm/region/cities', {
					parentCode: o,
				}, r => {
					this.cityList = r.repData
				}, e => {
					console.log(e);
				})
			},
			// 模糊搜索
			remoteMethod(query) {
				var self = this;
				if(query !== '') {
					this.loading = true;
					setTimeout(() => {
						self.$api.post('/crm/dealer/getDealerList', {
							dealerName: query,
							pageNum: 1,
							pageRows: 10
						}, res => {
							if(res.repData.length > 0) {
								self.options4 = res.repData
							} else {
								//								this.reqParams.missionName = "";
								this.options4 = [];
							}
							this.loading = false
						}, error => {
							self.$message.error('请求出错')
						})
					}, 200);
				} else {
					this.options4 = [];
				}
			},
			choise() {
				//				console.log(this.form.dealerCodes)
			}
		},
		created() {
			this.list = this.$route.query.list
			this.loadData();
		},
	}
</script>

<style scoped="scoped">
	.style1 {
		background: #D9524F;
		color: #fff;
	}
	
	.style2 {
		border-color: #D9524F;
		color: #D9524F;
	}
	
	.header {
		width: 96%;
		padding: 10px 20px;
		border-bottom: 1px solid #ECEDF2;
		font-size: 16px;
	}
	
	.header i {
		cursor: pointer
	}
	
	.header span {
		cursor: pointer
	}
	
	.el-form-item {
		width: 33%;
		/*float: left;*/
		display: inline-block;
	}
	
	.nav {
		padding: 20px;
	}
	
	.el-input {
		width: ;
	}
	
	.headerRight {
		text-align: center;
	}
</style>


